import { makeStyles, Backdrop, CircularProgress, Container, Typography } from '@material-ui/core';
import { useEffect, useState } from 'react';

const useStyles = makeStyles({
    otsikko : {
        marginTop: 10,
        marginBottom  : 20
    },
    tulos : {
        margin  : 15
    },
    nappi : {
        marginTop : 10,
        marginLeft : 10
    }
});

function Saatiedot() {

  const tyylit = useStyles();
  const [tulostus, setTulostus] = useState();
  const [ikoni, setIkoni] = useState();
  
  let [data, setData] = useState({
                                      paikkakunta : [],
                                      lampotila : [],
                                      kuvaus : [],
                                      kuva : [],
									                    virhe : null,
                                      tiedotHaettu : false
                                   });                    

  const haeTiedot = async () => {  

    try {

      const yhteys = await fetch("https://xamkbit.herokuapp.com/saatilanne/hamina");
      let tiedot = await yhteys.json();

      setData({
                ...data,
                paikkakunta : tiedot.name,
                lampotila : tiedot.main.temp,
                kuvaus : tiedot.weather[0].description,
                kuva : tiedot.weather[0].icon,
                virhe : null,
                tiedotHaettu : true
              });
  
setTulostus('Paikkakunnan ' + tiedot.name + 
              ' lmptila on ' + tiedot.main.temp + 
              ' astetta ja ' + tiedot.weather[0].description);

setIkoni('http://openweathermap.org/img/wn/' + tiedot.weather[0].icon + '@2x.png');

    } catch (error) {

      setData({
              ...data,
              paikkakunta : [],
              lampotila : [],
              kuvaus : [],
              kuva : [],
              tiedotHaettu : true,
              virhe : `VIRHE: yhteytt palvelimelle ei voitu muodostaa. (${error})`
      });
      
    }
    
  }

  useEffect(() => {

    haeTiedot();

  }, []);
  
  return (
    <Container>

      <Typography 
            variant="h3"
            className={tyylit.otsikko}      
      >Stiedot</Typography>

      {(data.virhe)
        ? <Typography color="error">
            {data.virhe}
          </Typography>
        : (data.tiedotHaettu) 
          ? <Typography 
              variant="h5"
              className={tyylit.tulos}
              >{tulostus}</Typography>
          : <Backdrop open={true}>
              <CircularProgress color="inherit" />
            </Backdrop>
      }
      <img src={ikoni}/>

    </Container>
  );
}

export default Saatiedot;
