import { Button, Typography } from '@material-ui/core';
import Otsikko from './Otsikko';
import { Link, useHistory, useParams } from 'react-router-dom';

function PoistaSaalis(props) {

    const history = useHistory();
    const { id } = useParams();

    const poistettavaSaalis = props.saaliit.filter((saalis) => {
                                return (saalis.id === id);
                            })[0];

    const kasitteleLomake = (e) => {

        e.preventDefault();

        let saaliitApu = props.saaliit.filter((saalis) => {
            return (saalis.id !== id);
        });
    
        props.setSaaliit([...saaliitApu]);
    
        history.push("/");

    }

    return (
        <form onSubmit={kasitteleLomake}>

        <Otsikko tyyli="pieni">Vahvista poisto</Otsikko>

        <Typography
            style={{marginBottom : 10}}
        >Haluatko varmasti poistaa kalasaaliin {poistettavaSaalis.nimi}?</Typography>

        <Button 
            type="submit"
            variant="contained"
            color="primary"
            fullWidth
            style={{marginBottom : 10}}
        >Poista kalasaalis</Button>

        <Button 
            component={Link}
            to="/"
            variant="contained"
            color="default"
            fullWidth
        >Peruuta</Button>

        </form>
    )
}

export default PoistaSaalis;
