import { Button, IconButton, List, ListItem, ListItemIcon, ListItemText } from "@material-ui/core";
import DeleteIcon from '@material-ui/icons/Delete';
import Otsikko from "./Otsikko";
import { Link } from 'react-router-dom';
import { format } from 'date-fns';

function Saalislista(props) {

    return (

        <>

        <Otsikko tyyli="pieni">Kalasaalis lista</Otsikko>

        <List>

        { (props.saaliit).map( (saalis, id) => {
  
          return (
                  <ListItem key={id}>
                      
                      <ListItemText primary={"Kala: " + saalis.nimi + "," + " Pyyntipaikka: " + saalis.paikka} 
                                    secondary={format(saalis.pyyntiaika, "d.M.y HH:mm") + " " + "Stiedot: "+
                                    "Paikkakunnan " + saalis.paikkakunta + " lmptila oli " +
                                    saalis.lampotila + " astetta " + " ja " + saalis.kuvaus}/>
                      <ListItemIcon>
                        <IconButton 
                          edge="end"
                          component={Link}
                          to={`/poista/${saalis.id}`} 
                        >
                            <DeleteIcon />
                        </IconButton>
                        </ListItemIcon>
                  </ListItem>
                );
  
        } ) }
  
        </List>

        <Button
          component={Link}
          to="/uusi"
          variant="contained"
          color="primary"
          fullWidth
          size="large"
        >Lis uusi kalasaalis</Button>
        
        </>
    )
}

export default Saalislista
