import { useState, useEffect } from 'react';
import { Container } from '@material-ui/core';
import UusiSaalis from './components/UusiSaalis';
import Saalislista from './components/Saalislista';
import Otsikko from './components/Otsikko';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import PoistaSaalis from './components/PoistaSaalis.js';
import Saatiedot from './components/Saatiedot.js';

function App() {

  const [saaliit, setSaaliit] = useState([]);

  const tallennaSaalislista = () => {

    localStorage.setItem("saalislista", JSON.stringify(saaliit));
  }

  const avaaSaalislista = () => {

    if (localStorage.getItem("saalislista")) {

      setSaaliit(JSON.parse(localStorage.getItem("saalislista")));

    } else {
      setSaaliit([]);
    }

  }

  useEffect(() => {
    
    avaaSaalislista();
    
  }, []);

  useEffect(() => {
    
    tallennaSaalislista();
    
  }, [saaliit]);


  return (
    <Router>
      <Container style={{maxWidth : "700px"}}>

        <Otsikko>Kalasaaliit</Otsikko>

        <Route path="/" exact>
          <Saalislista saaliit={saaliit} setSaaliit={setSaaliit}
                        tallennaSaalislista={tallennaSaalislista}/>
                        <Saatiedot/>
        </Route>

        <Route path="/uusi">
          <UusiSaalis saaliit={saaliit} setSaaliit={setSaaliit}/>
          <Saatiedot/>
        </Route>

        <Route path="/poista/:id">
          <PoistaSaalis saaliit={saaliit} setSaaliit={setSaaliit}/>
        </Route>

      </Container>
    </Router>
  );
}

export default App;
