import { useRef, useState } from 'react';
import { Button, TextField } from '@material-ui/core';
import Otsikko from './Otsikko';
import { Link, useHistory } from 'react-router-dom';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';
import { v4 as uuid} from 'uuid';

function UusiSaalis(props) {

    const [pvmaika] = useState(new Date());
    const tekstikentta = useRef({});
    const history = useHistory();

    const kasitteleLomake = (e) => {

        e.preventDefault();

        let uusiSaalisApu = {
                                id : uuid(),
                                nimi : tekstikentta.current.nimi,
                                paikka : tekstikentta.current.paikka,
                                pyyntiaika : pvmaika.getTime(),
                                paikkakunta : tekstikentta.current.paikkakunta,
                                lampotila : tekstikentta.current.lampotila,
                                kuvaus : tekstikentta.current.saakuvaus,
                                virhe : null,
                                tiedotHaettu : true
                             }
    
        props.setSaaliit([...props.saaliit, uusiSaalisApu]);
    
        history.push("/");

    }

    const syoteKasittelija = (e) => {

        tekstikentta.current[e.target.name] = e.target.value;

    }
    
    return (
        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
            <form onSubmit={kasitteleLomake}>

            <Otsikko tyyli="pieni">Lis uusi kalasaalis</Otsikko>

            <TextField 
                name="nimi"
                label="Kalan nimi"
                variant="outlined"
                fullWidth={true}
                inputRef={tekstikentta}
                onChange={syoteKasittelija}
                style={{marginBottom : 10}}
            />
            <TextField 
                name="paikka"
                label="Pyyntipaikka"
                variant="outlined"
                fullWidth={true}
                inputRef={tekstikentta}
                onChange={syoteKasittelija}
                style={{marginBottom : 10}}
            />
            <TextField 
                name="paikkakunta"
                label="Paikkakunta"
                variant="outlined"
                fullWidth={true}
                inputRef={tekstikentta}
                onChange={syoteKasittelija}
                style={{marginBottom : 10}}
            />
            <TextField 
                name="lampotila"
                label="Ulkolmptila"
                variant="outlined"
                fullWidth={true}
                inputRef={tekstikentta}
                onChange={syoteKasittelija}
                style={{marginBottom : 10}}
            />
            <TextField 
                name="saakuvaus"
                label="Sn kuvaus"
                variant="outlined"
                fullWidth={true}
                inputRef={tekstikentta}
                onChange={syoteKasittelija}
                style={{marginBottom : 10}}
            />

            <Button 
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
                style={{marginBottom : 10}}
            >Lis uusi kalasaalis</Button>

            <Button 
                component={Link}
                to="/"
                variant="contained"
                color="default"
                fullWidth
            >Peruuta</Button>

            </form>
        </MuiPickersUtilsProvider>
    )
}

export default UusiSaalis;
